.post {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid var(--twitter-background);
  padding-bottom: 10px;
  background-color: rgb(5, 26, 39) !important;
  padding: 20px;
}

.post__body {
  flex: auto;
  padding: 10px;
}

.post__avatar {
  padding: 20px;
  display: flex;
  margin-top: 5px;
}

.post__body > img {
  border-radius: 20px;
  width: 100%;
}

.post__footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.post__badge {
  font-size: 14px !important;
  color: var(--twitter-color);
}

.post__headerText > h3 {
  font-size: 15px;
  margin-bottom: 5px;
}

.post__headerSpecial {
  font-weight: 600;
  font-size: 12px;
  color: rgb(255, 255, 255);
}

.post__headerDescription {
  margin-bottom: 10px;
  font-size: 15px;
}
