@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  box-sizing: border-box;
}
body {
  background-color: rgb(5, 26, 39) !important;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
  align-items: center;
}


h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  /*border-color: #167bff;*/
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
.feed {
  flex:content;
  border-right: 1px solid;
  border-color: rgb(9, 44, 67) !important;
  color: rgb(202, 202, 202)!important;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  overflow-y: scroll;
}

.feed::-webkit-scrollbar {
  display: none;
}

.feed {
  -ms-overflow-style: none;
  scrollbar-width: none;
  max-width: 2000px;
  margin: 0;
}

.feed__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  border-right: 1px solid;
  border-color: rgb(9, 44, 67) !important;
  padding: 15px 20px;
  background-color: rgb(5, 26, 39) !important;
  color: rgb(202, 202, 202)!important;
}

.tweetBox {
  padding-bottom: 10px;
  border-bottom: 8px solid;
  border-color: rgb(9, 44, 67) ;
  padding-right: 10px;
}

.tweetBox > form {
  display: flex;
  flex-direction: column;
}

.tweetBox__input {
  padding: 20px;
  display: flex;
  background-color: rgb(5, 26, 39) !important;
}

.tweetBox__input > input {
  flex: 1 1;
  margin-left: 20px;
  font-size: 20px;
  border: none;
  outline: none;
  background-color: rgb(5, 26, 39) !important;
  color: rgb(202, 202, 202);
}

.tweetBox__imageInput {
  border: none;
  padding: 10px;
  background-color: rgb(5, 26, 39) !important;
  color: rgb(202, 202, 202) !important;
}

.tweetBox__tweetButton {
  background-color: rgb(0, 163, 223) !important;
  border: none !important;
  color: rgb(202, 202, 202) !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  border-radius: 30px !important;
  height: 40px !important;
  margin-top: 20px !important;
  width: 80px;
  margin-left: auto !important;
  -webkit-filter: opacity(0.8);
          filter: opacity(0.8);
}

.post {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid var(--twitter-background);
  padding-bottom: 10px;
  background-color: rgb(5, 26, 39) !important;
  padding: 20px;
}

.post__body {
  flex: auto;
  padding: 10px;
}

.post__avatar {
  padding: 20px;
  display: flex;
  margin-top: 5px;
}

.post__body > img {
  border-radius: 20px;
  width: 100%;
}

.post__footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.post__badge {
  font-size: 14px !important;
  color: var(--twitter-color);
}

.post__headerText > h3 {
  font-size: 15px;
  margin-bottom: 5px;
}

.post__headerSpecial {
  font-weight: 600;
  font-size: 12px;
  color: rgb(255, 255, 255);
}

.post__headerDescription {
  margin-bottom: 10px;
  font-size: 15px;
}

.sidebar {
  border-right: 1px solid;
  border-color: rgb(9, 44, 67) !important;
  flex: 0.3 1;
  padding-top: 20px !important;
  padding-left: 10px !important;
  padding-right: 10px !important;
  background-color: rgb(5, 26, 39);
  color: rgb(202, 202, 202);
  
}

.sidebar__twitterIcon {
  font-size: 40px !important;
  text-align: center;
  margin-left: 20px;
  color: rgb(0, 163, 223) !important;
}

.sidebar__tweet {
  background-color: rgb(0, 163, 223)!important;
  border: none !important;
  color: rgb(0, 163, 223)!important;
  font-weight: 400 !important;
  text-transform: inherit !important;
  border-radius: 100px !important;
  height: 30px !important;
  margin-top: 10px !important;
}

.sidebarOption {
  display: flex !important;
  padding: 5px !important;
  align-items: center !important;
  cursor: pointer !important;
  font-size: 30px !important;
}

.sidebarOption:hover {
  background-color: rgb(8, 58, 89)!important;
    border-radius: 100px !important;
    color: rgb(0, 163, 223) !important;
    transition: color 100ms ease-out !important;
}

.sidebarOption > .MuiSvgIcon-root {
  font-size: 40px !important;
  margin-right: 5px !important;
}

.sidebarOption > h2 {
  font-weight: 1000 !important;
  font-size: 20px !important;
  margin-right: 20px !important;
}

.sidebarOption--active {
  color: rgb(202, 202, 202)!important;
}

